import { Box, Theme } from '@material-ui/core'
import { makeStyles } from '@material-ui/styles'
import { Modal } from 'antd'
import OK from 'assets/images/ok-illutration.jpg'

interface Props {
    openModal: boolean
    onOk: () => void
    onCancel: () => void
    loading?: boolean
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {},
    wrapper: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',

        '& img': {
            width: '32%'
        },

        '& p': {
            fontSize: 14,
            textAlign: 'center',
            marginBottm: 8,
            lineHeight: 1.3
        },

        '& small': {
            fontSize: 13
        }
    }
}))

export const ModalConfirm = ({ openModal, onOk, onCancel, loading = false }: Props) => {
    const classes = useStyles()

    return (
        <Modal title='Xc nhn cp nht thng tin' visible={openModal} onCancel={onCancel} onOk={onOk} confirmLoading={loading} cancelText='Hy' okText='Xc nhn'>
            <Box className={classes.wrapper}>
                <img src={OK} alt='ok illutration' />

                <h3>Cm n bn  cung cp thng tin cho chng ti!!!</h3>

                <p>Thng tin bn cung cp c th hu ch vi nhng bn sinh vin c nhu cu tm nh tr.</p>

                <p>Hy m bo rng thng tin bn cung cp l chnh xc!</p>

                <small>Nhn vo nt <b>"Xc nhn"</b> bn di  thc hin yu cu cp nht thng tin.</small>
            </Box>
        </Modal>
    )
}
