import { yupResolver } from '@hookform/resolvers/yup'
import { Box, Button, CircularProgress, Container, Link, Theme, Typography } from '@material-ui/core'
import { Email, Facebook, Phone } from '@material-ui/icons'
import { Alert } from '@material-ui/lab'
import { makeStyles } from '@material-ui/styles'
import schoolApi from 'api/school'
import { useAppSelector } from 'app/hooks'
import { AutoCompleteField, CheckboxGroup, InputField } from 'components/FormFields'
import { FileInputField } from 'components/FormFields/FileInputField'
import { InputFieldIcon } from 'components/FormFields/InputFieldIcon'
import { RadioBtnField } from 'components/FormFields/RadioBtnField'
import { FieldOption, Motel } from 'models'
import { ChangeEvent, useEffect, useState } from 'react'
import { useFieldArray, useForm } from 'react-hook-form'
import { toast } from 'react-toastify'
import { checkSizeImg } from 'utils'
import * as yup from 'yup'
import { ReactComponent as Zalo } from 'assets/images/zalo.svg'
import { selectLoadingMotel } from '../../motelSlice'

interface Props {
    handleAddMotel: (data: any) => void;
    handleUploadThumbnail: (image: any) => void;
    handleUploadImages: (images: any) => void
}

const initialMotel: Motel = {
    name: '',
    images: [],
    address: '',
    thumbnail: '',
    room: [{
        area: {
            width: 0,
            length: 0,
        },
        optional: [],
        remain: 0,
        price: 0,
        total: 0
    }],
    desc: '',
    contact: {
        phone: '',
        zalo: '',
        facebook: '',
        email: ''
    },
    status: 'yes',
    available: 0,
    school: [],
}

const radioOptions: Array<FieldOption> = [
    {
        label: 'Cn trng',
        value: 'yes'
    },
    {
        label: 'Ht phng',
        value: 'no'
    }
]

const checkboxOptions: Array<FieldOption> = [
    {
        label: 'Wifi',
        value: 'wifi'
    },
    {
        label: 'My lnh',
        value: 'ml'
    },
    {
        label: 'Gc',
        value: 'gac'
    },
    {
        label: 'Nh xe',
        value: 'nx'
    },
    {
        label: 'Camera',
        value: 'camera'
    },
    {
        label: 'Qut',
        value: 'quat'
    },
    {
        label: 'Trn lu',
        value: 'tl'
    },
    {
        label: 'Ging',
        value: 'giuong'
    },
    {
        label: 'Ging tng',
        value: 'gt'
    },
    {
        label: 'Chung ch',
        value: 'cc'
    },
    {
        label: 'Dng c v sinh',
        value: 'dcvs'
    }
]

const initialCheckbox: any = {
    wifi: false,
    ml: false,
    gac: false,
    nx: false,
    camera: false,
    quat: false,
    tl: false,
    giuong: false,
    gt: false,
    cc: false,
    dcvs: false
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {},
    wrap: {
        border: '1px solid',
        padding: theme.spacing(1.5),
        position: 'relative',
        borderRadius: 5
    },
    titleWrap: {
        position: 'absolute',
        left: 6,
        top: -14,
        fontSize: 18,
        display: 'inline-block',
        background: '#fff',
        paddingLeft: 4,
        paddingRight: 4
    },
    imgsWrap: {
        display: 'grid',
        gridTemplateColumns: 'repeat(4, 1fr)',
        gridGap: theme.spacing(1),

        "& > div": {
            overflow: 'hidden',
            maxHeight: 200,
            outline: '1px solid #000',
        }
    }
}))

const schema = yup.object().shape({
    name: yup
        .string()
        .strict()
        .trim('Tn khng c cha khong trng  u v cui')
        .max(50, 'Ti a 70 k t')
        .required('Hy thm tn nh tr'),
    address: yup
        .string()
        .strict()
        .trim('a ch khng c cha khong trng  u v cui')
        .max(50, 'Ti a 150 k t')
        .required('Hy thm a ch'),
    desc: yup
        .string()
        .required('Hy vit mt t m t v nh tr ca bn. Gip cho mi ngi tm kim nh tr chnh xc hn')
        .strict()
        .trim('M t khng c cha khong trng  u v cui'),
    contact: yup
        .object()
        .shape({
            phone: yup
                .string()
                .strict()
                .trim('a ch khng c cha khong trng  u v cui'),
            email: yup
                .string()
                .email('Email khng hp l'),
            zalo: yup
                .string()
                .strict()
                .trim('a ch khng c cha khong trng  u v cui'),
            facebook: yup
                .string()
                .strict()
                .trim('a ch khng c cha khong trng  u v cui')
        }),
    room: yup
        .array()
        .of(
            yup
                .object()
                .shape({
                    area: yup
                        .object()
                        .shape({
                            width: yup
                                .number()
                                .min(1, 'Chiu rng ti thiu 1 mt')
                                .max(99, 'Chiu rng ti a l 99 mt'),
                            length: yup
                                .number()
                                .min(1, 'Chiu di ti thiu 1 mt')
                                .max(99, 'Chiu di ti a l 99 mt')
                        }),
                    total: yup
                        .number()
                        .min(1, 'S phng ti thiu l 1 phng')
                        .max(99, 'S phng ti a l 99 phng'),
                    remain: yup
                        .number()
                        .max(99, 'S phng ti a l 99 phng')
                        .test('check-remain-room', 'S phng trng khng c ln hn tng s phng', function (value: any) {
                            return this.parent.total >= value
                        }),
                    price: yup
                        .number()
                        .min(10000, 'S tin ti thiu l 10.000 ng')
                        .max(99000000, 'S tin ti a l 99.000.000 ng'),
                })),
    school: yup
        .array()
        .min(1, 'Hy cung cp trng hc xung quanh khu vc nh tr')
})

export const AddForm = ({ handleAddMotel, handleUploadImages, handleUploadThumbnail }: Props) => {
    const classes = useStyles()

    const loading = useAppSelector(selectLoadingMotel)
    const { control, handleSubmit, setValue, formState: { errors }, setError } = useForm<Motel>({
        defaultValues: initialMotel,
        resolver: yupResolver(schema),
    })
    const { fields, append } = useFieldArray({
        control,
        name: 'room'
    })

    const [status, setStatus] = useState('yes')
    const [optionsSchool, setOptionsSchool] = useState<Array<any>>([])
    const [selectedSchool, setSelectedSchool] = useState<Array<any>>([])

    const [selectedImages, setSelectedImages] = useState<any>()
    const [errThumnail, setErrThumbnail] = useState('')
    const [errImages, setErrImages] = useState('')

    useEffect(() => {
        schoolApi.getAll()
            .then((res) => {
                setOptionsSchool(res.data)
            })
            .catch((err) => {
                toast.error(err.response.data.message)
            })
    }, [])

    const handleChangeRadio = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.value === 'no')
            setValue('available', 0)
        setStatus(e.target.value)
    }

    const handleChangeThumbnail = (e: ChangeEvent<HTMLInputElement>) => {
        if (!checkSizeImg(e.target.files)) {
            setErrThumbnail('Kch thc nh khng c vt qu 500KB')
        } else {
            setErrThumbnail('')
            handleUploadThumbnail(e.target.files)
        }
    }

    const handleChangeImages = (e: ChangeEvent<HTMLInputElement>) => {
        const files = e.target.files

        if (files && files.length !== 0) {
            if (files?.length > 8) {
                setErrImages('Ch c chn ti  8 nh')
            } else if (!checkSizeImg(files)) {
                setErrImages('Kch thc mi nh khng c vt qu 500KB')
            } else {
                setSelectedImages(files)
                setErrImages('')
                handleUploadImages(files)
            }
        } else {
            setSelectedImages([])
        }
    }

    const handleSelectSchool = (e: any, value: any) => {
        value?.length > 0 && setError('school', { type: '' })
        setSelectedSchool(value)
        setValue('school', value)
    }

    const appendClick = () => {
        append({
            area: {
                width: 0,
                length: 0,
            },
            optional: [],
            remain: 0,
            price: 0,
            total: 0
        })
    }

    return (
        <Container maxWidth='md'>
            <Box className={classes.root}>
                <Typography variant='h3'>
                    Thm nh tr
                </Typography>

                <form onSubmit={handleSubmit(handleAddMotel)}>
                    <Box className={classes.wrap} mt={3}>
                        <Typography className={classes.titleWrap}>
                            Thng tin nh tr
                        </Typography>

                        <Alert severity="info">Cm n bn  h tr chng ti c thm thng tin v nh tr. Thng tin ca bn cung cp s gip cho cc bn sinh vin c th d dng tm c nh tr cho mnh. V hy m bm nhng thng tin bn cung cp l chnh xc.</Alert>

                        <Box>
                            <InputField name='name' label='Tn nh tr' type='text' control={control} required />
                            <InputField name='address' label='a ch' type='text' control={control} required />

                            <AutoCompleteField label="Trng hc ln cn" title='name' error={errors.school?.message} freeSolo value={selectedSchool} onChange={handleSelectSchool} multiple disabled={optionsSchool.length > 0 ? false : true} options={optionsSchool} />

                            <InputField name='desc' label='M t nh tr' type='text' multiline={true} rows={4} control={control} />

                            <Box className={classes.wrap} mt={3}>
                                <Typography className={classes.titleWrap} style={{ fontSize: 16 }}>
                                    Thng tin lin h
                                </Typography>

                                <Alert severity="info">Hy thm t nht mt thng tin lin h. Gip cho vic tra cu thng tin chnh xc hn</Alert>

                                <Box>
                                    <InputFieldIcon name='contact.phone' label='in thoi' type="text" control={control} placeholder='0123456789' icon={<Phone />} />
                                    <InputFieldIcon name='contact.email' label='Email' type="email" control={control} placeholder='abc@gmail.com' icon={<Email />} />
                                    <InputFieldIcon name='contact.facebook' label='Facebook' type="text" control={control} placeholder='Gn ng dn n facebook ca bn' icon={<Facebook />} />
                                    <InputFieldIcon name='contact.zalo' label='Zalo' placeholder='S in thoi zalo ca bn' type="text" control={control} icon={<Zalo />} />
                                </Box>
                            </Box>

                            <RadioBtnField name='status' label="Trng thi" handleChange={handleChangeRadio} options={radioOptions} control={control} />

                            {status === 'yes' && <InputField name='available' label='S phng trng' type='number' min={0} max={99} control={control} />}

                            <FileInputField name='thumbnail' label="nh gii thiu" accept='.png, .jpg, .jpeg' error={errThumnail} onChange={handleChangeThumbnail} required />

                            <FileInputField name='images' label="nh khc" accept='.png, .jpg, .jpeg' error={errImages} onChange={handleChangeImages} multiple={true} />

                            <Box className={classes.imgsWrap}>
                                {selectedImages && [...selectedImages].map((img, index) => (
                                    <Box component='div'>
                                        <img key={index} src={URL.createObjectURL(img)} alt='tm nh tr sinh vin' />
                                    </Box>
                                ))
                                }
                            </Box>
                        </Box>
                    </Box>

                    {fields.map((field, index) => (
                        <>
                            <Box className={classes.wrap} mt={3}>
                                <Typography className={classes.titleWrap}>
                                    {`Thng tin phng tr ${index + 1}`}
                                </Typography>

                                {index === 0 && <Alert severity="info">Nu nh nh tr ca bn c nhiu loi phng tr, bn c th nhn vo nt "Thm phng tr" pha di  b sung thm thng tin</Alert>}

                                <Box>
                                    <Box className={classes.wrap} mt={3}>
                                        <Typography className={classes.titleWrap} style={{ fontSize: 16 }}>
                                            {`Din tch(m)`}
                                        </Typography>

                                        <Box>
                                            <InputField key={field.id} name={`room.${index}.area.length`} label='Chiu di' type='number' min={0} max={99} control={control} required />
                                            <InputField key={field.id} name={`room.${index}.area.width`} label='Chiu rng' type='number' min={0} max={99} control={control} required />
                                        </Box>
                                    </Box>

                                    <InputField key={field.id} name={`room.${index}.total`} label='S lng phng' type='number' min={0} max={99} step={1} control={control} required />
                                    <InputField key={field.id} name={`room.${index}.remain`} label='S lng phng trng' type='number' min={0} max={99} step={1} control={control} required />
                                    <InputField key={field.id} name={`room.${index}.price`} label='Gi thu' type='number' min={0} max={99000000} step={10000} control={control} required />

                                    <CheckboxGroup key={field.id} label='Thng tin b sung' initialState={initialCheckbox} name={`room.${index}.optional`} control={control} options={checkboxOptions} />
                                </Box>
                            </Box>
                            <Box style={{ textAlign: 'right' }}>
                                <Link onClick={appendClick}>&#x2b; Thm phng tr</Link>
                            </Box>
                        </>
                    ))}

                    <Box my={3}>
                        <Button variant='contained' size='large' color='primary' type='submit' fullWidth disabled={loading}>
                            {loading && <><CircularProgress color='secondary' size={20} /> &nbsp;</>}
                            Xc nhn
                        </Button>
                    </Box>
                </form>
            </Box>
        </Container>
    )
}
